import axios from "axios"
import {useEffect, useState} from "react"
import "./sidebar.css"
import {Link} from "react-router-dom"


export default function Sidebar() {
  const [cats, setCats] = useState([])

  useEffect(()=>{
    const getCats = async () =>{
      const res =await axios.get("/categories");
      setCats(res.data)
    }
    getCats()
  },[])
    return (
      <div className="sidebar">
        <div className="sidebarItem">
          <span className="sidebarTitle">ABOUT ME</span>
          <img
            src="https://i.pinimg.com/originals/02/29/b8/0229b842a4ca1beb50cf7d0f2445f41a.jpg"
            alt=""
          />
          <p>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. In
            reiciendis fuga dolorum voluptatum! Perferendis ex libero
            voluptatibus, sapiente nihil odio, porro at aliquam earum soluta
            quaerat animi distinctio enim rerum!
          </p>
        </div>
        <div className="sidebarItem">
          <span className="sidebarTitle">CATEGORIES</span>
          <ul className="sidebarList">
            {cats.map((c) =>(
              <Link to={"/?cat="+ c.name} className="link">
                <li className="sidebarListItem">{c.name}</li>
              </Link>
            ))}
          </ul>
        </div>
        <div className="sidebarItem">
          <span className="sidebarTitle">FOLLOW US</span>
          <div className="sidebarSocial">
            <i className="sidebarIcon fab fa-facebook-square"></i>
            <i className="sidebarIcon fab fa-twitter-square"></i>
            <i className="sidebarIcon fab fa-pinterest-square"></i>
            <i className="sidebarIcon fab fa-instagram-square"></i>
          </div>
        </div>
      </div>
    );
}
